<template>
  <g-body-layout
    :pageIndex="pageNumber"
    :pageSize="pageSize"
    :total="total"
    :filterText.sync="filterText"
    @pageIndexChange="pageIndexChange"
    @pageSizeChange="pageSizeChange"
    @suffixSearchCallback="onFilterReady"
  >
    <!-- 按钮组插槽 -->
    <template #btnGroup>
      <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
      </g-btn-group>
    </template>
    <!-- g-ndo-table插槽 -->
    <template #table>
      <g-ndo-table
        :id="id"
        :handName="'工艺建模'"
        :dataList="dataList"
        :isTableLoading="loading"
        :total="total"
        @rowClick="rowClick"
      ></g-ndo-table>
    </template>
    <!-- 详细内容插槽 -->
    <template #content>
      <create-or-edit-craft
        :ndoEditParams="ndoEditParams"
        :refreshTag="refreshTag"
        :saveTag="saveTag"
        :resetTag="resetTag"
        @childRefresh="childRefresh($event)"
        @rowClick="rowClick($event)"
        v-if="isShowEdit"
      ></create-or-edit-craft>
      <g-empty v-else> </g-empty>
    </template>
  </g-body-layout>
</template>

<script>
import CreateOrEditCraft from "./create-or-edit-craft/create-or-edit-craft.vue";
import GCopyNdo from "@/components/g-copy-ndo/g-copy-ndo.vue";
import { GNdoPageBase } from "@/utils";

export default {
  name: "ProcessCraft",
  mixins: [GNdoPageBase],
  components: {
    CreateOrEditCraft,
  },

  data() {
    return {
      isTableLoading: false,
      filterText: "",
    };
  },

  mounted() {},
  created() {
    this.getBtnList();
  },
  methods: {
    getBtnList() {
      // 获取按钮列表
      this.btnList = [
        {
          text: this.AppConst.button.create,
          icon: this.AppConst.icon.create,
          eventName: this.AppConst.button.create,
          display: true,
        },
        {
          text: this.AppConst.button.copy,
          icon: this.AppConst.icon.copy,
          eventName: this.AppConst.button.copy,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.delete,
          icon: this.AppConst.icon.delete,
          eventName: this.AppConst.button.delete,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.refresh,
          icon: this.AppConst.icon.refresh,
          eventName: this.AppConst.button.refresh,
          right: true,
          display: this.isShowEdit,
        },
        {
          text: this.AppConst.button.save,
          icon: this.AppConst.icon.save,
          eventName: this.AppConst.button.save,
          right: true,
          type: "primary",
          display: this.isShowEdit,
        },
      ];
    },
    copy() {
      this.createModal(
        { title: "复制", width: "600px" },
        {
          component: GCopyNdo,
          props: {
            entity: {
              copyValue: this.dataList[0].name,
              value: this.dataList[0].name,
            },
          },
          on: {
            success: (res) => {
              console.log(res);
              // 手动延时
              this.loading = true;
              setTimeout(() => {
                this.loading = false;
                this.copySuccess(res.value, res.copyValue);
                this.refreshGoFirstPage();
              }, 500);
            },
          },
        }
      );
    },
    delete() {
      // 删除确认
      this.$confirm({
        title: "确认删除",
        content: "确认删除该工艺吗？删除后无法恢复。",
        onOk: () => {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            // 消息提示
            this.deleteSuccess();
            // 刷新
            this.refresh();
          }, 500);
        },
      });
    },
    fetchData(request) {
      this.loading = true;
      setTimeout(() => {
        request.finishedCallback();
        // 填充dataList 示例数据
        let dataList = [];
        const craftTypes = [
          "机械加工工艺",
          "装配工艺",
          "焊接工艺",
          "热处理工艺",
          "表面处理工艺",
          "检验工艺"
        ];
        
        const productTypes = [
          "金属结构件",
          "电子产品",
          "机械设备",
          "汽车零部件",
          "航空零件",
          "医疗器械"
        ];
        
        const statuses = ["已发布", "草稿", "审核中", "已废弃"];
        
        for (let i = 0; i < 25; i++) {
          const typeIndex = i % 6;
          let craftCode, name, description, version;
          
          // 根据工艺类型生成不同的工艺数据
          switch(typeIndex) {
            case 0: // 机械加工工艺
              const machiningCrafts = [
                { code: "MC001", name: "车削加工工艺", desc: "普通车床加工工艺流程规范", ver: "V1.2" },
                { code: "MC002", name: "铣削加工工艺", desc: "立式铣床加工工艺规范", ver: "V2.0" },
                { code: "MC003", name: "钻孔加工工艺", desc: "多轴钻床加工工艺规范", ver: "V1.5" },
                { code: "MC004", name: "磨削加工工艺", desc: "精密磨床加工工艺规范", ver: "V1.1" }
              ];
              const machiningIndex = i % 4;
              craftCode = machiningCrafts[machiningIndex].code;
              name = machiningCrafts[machiningIndex].name;
              description = machiningCrafts[machiningIndex].desc;
              version = machiningCrafts[machiningIndex].ver;
              break;
            
            case 1: // 装配工艺
              const assemblyCrafts = [
                { code: "AC001", name: "机械装配工艺", desc: "精密机械部件装配工艺规范", ver: "V1.3" },
                { code: "AC002", name: "电子装配工艺", desc: "PCB电子元件装配工艺规范", ver: "V2.1" },
                { code: "AC003", name: "液压系统装配工艺", desc: "液压系统组装工艺规范", ver: "V1.0" }
              ];
              const assemblyIndex = i % 3;
              craftCode = assemblyCrafts[assemblyIndex].code;
              name = assemblyCrafts[assemblyIndex].name;
              description = assemblyCrafts[assemblyIndex].desc;
              version = assemblyCrafts[assemblyIndex].ver;
              break;
            
            case 2: // 焊接工艺
              const weldingCrafts = [
                { code: "WC001", name: "气体保护焊工艺", desc: "气体保护焊接工艺规范", ver: "V1.4" },
                { code: "WC002", name: "电弧焊工艺", desc: "电弧焊接工艺规范", ver: "V1.8" },
                { code: "WC003", name: "激光焊接工艺", desc: "激光焊接工艺规范", ver: "V2.2" }
              ];
              const weldingIndex = i % 3;
              craftCode = weldingCrafts[weldingIndex].code;
              name = weldingCrafts[weldingIndex].name;
              description = weldingCrafts[weldingIndex].desc;
              version = weldingCrafts[weldingIndex].ver;
              break;
            
            case 3: // 热处理工艺
              const heatCrafts = [
                { code: "HC001", name: "淬火工艺", desc: "金属材料淬火工艺规范", ver: "V1.3" },
                { code: "HC002", name: "回火工艺", desc: "金属材料回火工艺规范", ver: "V1.7" },
                { code: "HC003", name: "退火工艺", desc: "金属材料退火工艺规范", ver: "V1.2" }
              ];
              const heatIndex = i % 3;
              craftCode = heatCrafts[heatIndex].code;
              name = heatCrafts[heatIndex].name;
              description = heatCrafts[heatIndex].desc;
              version = heatCrafts[heatIndex].ver;
              break;
            
            case 4: // 表面处理工艺
              const surfaceCrafts = [
                { code: "SC001", name: "喷漆工艺", desc: "产品表面喷漆工艺规范", ver: "V1.5" },
                { code: "SC002", name: "电镀工艺", desc: "金属表面电镀工艺规范", ver: "V1.9" },
                { code: "SC003", name: "表面抛光工艺", desc: "金属表面抛光工艺规范", ver: "V1.4" }
              ];
              const surfaceIndex = i % 3;
              craftCode = surfaceCrafts[surfaceIndex].code;
              name = surfaceCrafts[surfaceIndex].name;
              description = surfaceCrafts[surfaceIndex].desc;
              version = surfaceCrafts[surfaceIndex].ver;
              break;
            
            case 5: // 检验工艺
              const inspectionCrafts = [
                { code: "IC001", name: "尺寸检验工艺", desc: "产品尺寸检验工艺规范", ver: "V1.6" },
                { code: "IC002", name: "无损检测工艺", desc: "产品无损检测工艺规范", ver: "V2.0" },
                { code: "IC003", name: "功能测试工艺", desc: "产品功能测试工艺规范", ver: "V1.3" }
              ];
              const inspectionIndex = i % 3;
              craftCode = inspectionCrafts[inspectionIndex].code;
              name = inspectionCrafts[inspectionIndex].name;
              description = inspectionCrafts[inspectionIndex].desc;
              version = inspectionCrafts[inspectionIndex].ver;
              break;
          }
          
          dataList[i] = {
            id: i + 1,
            craftCode: `${craftCode}${String(i + 1).padStart(2, "0")}`,
            name: name,
            craftType: craftTypes[typeIndex],
            productType: productTypes[i % 6],
            description: description,
            version: version,
            operationCount: 3 + (i % 8),
            status: statuses[i % 4],
            createdBy: "admin",
            createdTime: "2024-03-19 " + (10 + i % 10) + ":" + (i % 60),
            modifiedBy: "admin",
            modifiedTime: "2024-03-" + (20 + i % 8) + " " + (13 + i % 10) + ":" + (i % 60),
          };
        }

        // filter 过滤
        if (this.filterText) {
          console.log(this.filterText);
          dataList = dataList.filter((item) => {
            return item.craftCode.indexOf(this.filterText) > -1 || 
                   item.name.indexOf(this.filterText) > -1 ||
                   item.craftType.indexOf(this.filterText) > -1 ||
                   item.productType.indexOf(this.filterText) > -1 ||
                   item.description.indexOf(this.filterText) > -1 ||
                   item.version.indexOf(this.filterText) > -1;
          });
        }

        request.successCallback({
          items: dataList,
          totalCount: dataList.length * 2,
        });
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped></style> 